      getDetail() {
        let uid = this.$route.query.uid;
        let frompage = this.$route.query.frompage;
        if(frompage == '' || frompage == null || frompage == undefined){
          frompage = '1';
        }
        this.currentStatus = frompage;
        this.$http.get('/api/v1.0/client/queryApplyDetail/'+uid).then(response => { //
          if (response.body.code === 200) {
            let data = response.body.result;
            this.approveAllData = response.body.result;
            this.hasAbnormal = data.abnormalAttendApproval.hasAbnormal;
            this.datePunchCardLogs = data.abnormalAttendApproval.datePunchCardLogs;
            this.dateApplys = data.abnormalAttendApproval.dateApplys;
            this.attendReport = data.abnormalAttendApproval.attendReport;
            this.connectTime = data.abnormalAttendApproval;

            this.$http.get('/api/v1.0/client/queryApprovalForm/'+response.body.result.approvalType).then(response => {
              if (response.body.code === 200) {
                this.fieldsdata = response.body.result;
                let approvalType = response.body.result.approvalType;
                let configType = response.body.result.configType;
                // configType = 1;
                this.configType = configType; //
                this.approvalperson(configType,approvalType);
              }
            }, response => {
            });

            //
            // this.approvaloutside('43');
            //
            this.approvaloutside('43');

          }
        }, response => {
        });
